import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { Component, OnInit, ChangeDetectorRef, Input, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import jsbarcode from 'jsbarcode';
import printJS from 'print-js';
import { Platform } from '@ionic/angular';
declare var electron: any;

@Component({
  selector: 'app-print-four-col',
  templateUrl: './print-four-col.component.html',
  styleUrls: ['./print-four-col.component.scss']
})
export class PrintFourColComponent implements OnInit {
  propertySelected: any;
  @Input() start_print : boolean = false
  @Input() stamp: any;
  listRowBarcode = [{show_col2:false,show_col3:false,show_col4:false}]; // cha s hng hin th. true th hin th 2 tem. false l hien th 1 tem trn 1 hng
  heightScroll :any;
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private cdf: ChangeDetectorRef,
    private vhComponent:VhComponent,
    private vhQuery:VhQuery,
    public platform: Platform
  ) {
    // this.stamp = this.router.getCurrentNavigation().extras.state;
    // this.stamp.barcode.property =  this.vhAlgorithm.sortNumberbyASC(this.stamp.barcode.property,'y')
  }

  ngOnChanges({start_print}: SimpleChanges): void {
    if(start_print && start_print.currentValue) {
      this.vhComponent.showLoading('')
      .then(() =>{
       this.stamp.barcode.property =  this.vhAlgorithm.sortNumberbyASC(this.stamp.barcode.property,'y')
       for(let item of this.stamp.barcode.property){
        switch(item.type){
          case 'barcode-text': item.value = this.stamp.formProduct.barcode;break;
          case 'price': item.value = this.stamp.formProduct.price;break;
          case 'barcode': item.value = this.stamp.formProduct.barcode;break;
          case 'name': item.value = this.stamp.formProduct.name;break;
        }
      }
       this.propertySelected = this.stamp.barcode.property[0];
       this.changeNummber()
       setTimeout(() => {
        this.renderBarcode(); 
      }, 1000);
       setTimeout(() => {
         this.vhComponent.hideLoading(0)
         this.printBrowser();
       }, 3000);
      })
     
    }
   }

  ngOnInit(): void {
     
  }

  changeNummber() {
    /**
     * nu chia ht cho 4 th hin c 4 tem trn hng
     * ngc li  => khi push ti hng cui cng th check i = (number-1)/3 : ct th 1  hay = (number-2)/3 : ct th 2
     * 
     */
    this.listRowBarcode = []
    for (let i = 0; i < this.stamp.number/4; i++) {
      
      
     if(this.stamp.number%4 ==0)  this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:true});
     else {
       if(i == (this.stamp.number-1)/4) this.listRowBarcode.push({show_col2:false,show_col3:false,show_col4:false});
       else if(i == (this.stamp.number-2)/4) this.listRowBarcode.push({show_col2:true,show_col3:false,show_col4:false});
       else  if(i == (this.stamp.number-3)/4) this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:false});
       else this.listRowBarcode.push({show_col2:true,show_col3:true,show_col4:true});
     }
    }
    
  }
 

  renderBarcode() {
    if(this.stamp.formProduct.barcode)
    jsbarcode(".barcode",  this.stamp.formProduct.barcode, {
      displayValue: false,
      // height: 65
    });
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/design-barcode/detail'],{state: this.stamp.barcode})
  }
  Print() {
    if (this.platform.is("electron")) this.printElectron()
    else this.printBrowser()
  }
  printElectron() {
    let html = document.getElementById("stamp-barcode-electron").innerHTML;
    electron.ipcRenderer.send("print-start", {
      html: html,
      deviceName: this.stamp.printer_name,
      
    });
    electron.ipcRenderer.on("print-done", (event, data) => {
      if (!data) this.vhComponent.alertMessageDesktop('warning', 'Printing failed', 3000)
     
    });
  }

  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height > 20 ? this.stamp.height - 4 : this.stamp.height -2 ) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }

}
