import { DetailComponent } from './detail/detail.component';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PrintBarcodeComponent } from './print-barcode.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { PrintTwoColComponent } from './print-two-col/print-two-col.component';
import { PrintThreeColComponent } from './print-three-col/print-three-col.component';
import { PrintOneColComponent } from './print-one-col/print-one-col.component';
import { PrintFourColComponent } from './print-four-col/print-four-col.component';
import { PrintFiveColComponent } from './print-five-col/print-five-col.component';

const routes: Routes = [
  {
    path: '',
    component: PrintBarcodeComponent
  },
]

@NgModule({
  declarations: [
    PrintBarcodeComponent,
    DetailComponent,
    PrintTwoColComponent,
    PrintThreeColComponent,
    PrintOneColComponent,
    PrintFourColComponent,
    PrintFiveColComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    FormsModule,
    IonicModule,
    TranslateModule,
  ]
})
export class PrintBarcodeModule { }
